    const formatPAN = (inputText) => {
        // Apply PAN format: first five characters as letters, next four as numbers, last as a letter
        let formattedPAN = inputText.slice(0, 10); // Maximum of 10 characters
        formattedPAN = formattedPAN.replace(/[^A-Z0-9]/g, ''); // Remove any non-alphanumeric characters

        // Ensure first five characters are letters, next four are numbers, and last one is a letter
        let firstPart = formattedPAN.slice(0, 5).replace(/[^A-Z]/g, '');
        let middlePart = formattedPAN.slice(5, 9).replace(/[^0-9]/g, '');
        let lastPart = formattedPAN.slice(9, 10).replace(/[^A-Z]/g, '');

        return `${firstPart}${middlePart}${lastPart}`; // Combine formatted parts
    };
