   const formatAadharNumber = (input) => {
    // Remove non-digit characters
    const sanitizedInput = input.replace(/\D/g, '');

    // If the input has more than 12 digits, truncate it
    const truncatedInput = sanitizedInput.slice(0, 12);

    // Format Aadhar number to display groups of 4 digits separated by spaces
    const formattedNumber = truncatedInput.replace(/(\d{4})(?=\d)/g, '$1 ');

    return formattedNumber;


};
