const formatTAN = (inputText) => {
    let formatTAN  = inputText.slice(0, 10).toUpperCase(); // Convert input to uppercase
    formatTAN  = formatTAN.replace(/[^A-Z0-9]/g, ''); // Remove any non-alphanumeric characters

    // Ensure first five characters are letters, next four are numbers, and last one is a letter
    let firstPart = formatTAN .slice(0, 5).replace(/[^A-Z]/g, '');
    let middlePart =formatTAN .slice(5, 9).replace(/[^0-9]/g, '');
    let lastPart = formatTAN .slice(9, 10).replace(/[^A-Z]/g, '');

    return `${firstPart}${middlePart}${lastPart}`; // Combine formatted parts
};



