    const calculateFinalHouseProperty = () => {
        // Perform the calculation for "Deemed Let Out"
        const { "Rent Received": rentReceived, "Property Tax": propertyTax, "Interest on House Loan": interestOnLoan } = DeemedLetOutFields;
    
        // Calculate total rent received after subtracting 30% for expenses
        const effectiveRentReceived = rentReceived - (rentReceived * 0.3);
    
        // Calculate property tax as a deduction
        const propertyTaxAfterExpenses = propertyTax - (propertyTax * 0.3);
    
        const totalDeemedLetOut = effectiveRentReceived - parseFloat(propertyTaxAfterExpenses) - parseFloat(interestOnLoan);
    
        // Set the calculated total for "Deemed Let Out" in the house property fields
        const houseProperty = rentedHousePropertyFields["Rent Received"] -
                              rentedHousePropertyFields["Property Tax"] -
                              rentedHousePropertyFields["Interest on House Loan"];
        const repairCharges = (rentedHousePropertyFields["Rent Received"] - rentedHousePropertyFields["Property Tax"]) * 30 / 100;
        const houseProperty1 = houseProperty - repairCharges;
        const finalHouseProperty = houseProperty1 - housePropertyFields["Self Occupied"] + totalDeemedLetOut;
    
        // Update the state with the calculated value
        setFinalHouseProperty(finalHouseProperty);
    };
    
    // Call calculateFinalHouseProperty when rentedHousePropertyFields or housePropertyFields change
    useEffect(() => {
        calculateFinalHouseProperty();
    }, [rentedHousePropertyFields, housePropertyFields]);
    
    // Call calculateFinalHouseProperty when DeemedLetOutFields change
    useEffect(() => {
        calculateFinalHouseProperty();
    }, [DeemedLetOutFields]);


