const handleClosePopupDivident = () => {
    const totals = computeTotals();
    const totalValue = totals.totalEarnings + totals.totalUnits + totals.totalInvestment;

    setOpenPopupDevident(false); // Close the popup

    // Find the index of the last "Divident" row for this instance
    const lastDividentIndex = incomeRowsOtherSourses.reduce((acc, row, index) => {
        if (row.Details === "Divident") {
            return index; // Keep track of the last "Divident" row index
        }
        return acc;
    }, -1);

    // Create a new array to update incomeRowsOtherSourses
    let updatedRows = [...incomeRowsOtherSourses];

    // Check if there's an existing "Divident" row for this instance
    if (lastDividentIndex !== -1) {
        // Update the existing "Divident" row's "Net Earning" with totalValue
        updatedRows[lastDividentIndex]["Net Earning"] = totalValue.toString();
    } else {
        // If no "Divident" row exists, create a new one with initial "Net Earning" as "0"
        updatedRows.push({ "S/n": updatedRows.length + 1, Details: "Divident", "Net Earning": "0" });
    }

    // Add a new row for "Select Option" after handling "Divident" rows
    updatedRows.push({ "S/n": updatedRows.length + 1, Details: "Select Option", "Net Earning": "" });

    // Update incomeRowsOtherSourses with the modified rows
    setIncomeRowsOtherSourses(updatedRows);

    // Update popupFields_7 with netEarnings
    setPopupFields_7({ ...popupFields_7, netEarnings: totalValue });

    // Update totalDividentEarnings state with the calculated total
        const totalNetEarnings = calculateTotalNetEarnings(updatedRows);
        setTotalDividentEarnings(totalNetEarnings);


};





 // Function to calculate totals
 const computeTotals = () => {
     let totalEarnings = 0;
     let totalUnits = 0;
     let totalInvestment = 0;
 
     rows.forEach(row => {
         totalEarnings += parseFloat(row.earnings || 0);
         totalUnits += parseFloat(row.units || 0);
         totalInvestment += parseFloat(row.investmentValue || 0);
     });
 
     return {
         totalEarnings,
         totalUnits,
         totalInvestment
     };
 };
 
